<template>
  <div style="padding:0 0 15px 0;background-color:#F6F6F6;font-size:0.8rem;">
    <div class="arrive">
    </div>
    <div id="status_box">
      <span style="display:inline-block;width:30%;-border:1px solid red;">结算日</span>
      <span style="display:inline-block;width:30%;-border:1px solid red;">状态</span>
      <span style="display:inline-block;width:30%;-border:1px solid red;">金额</span>
    </div>
    <!-- 月份 -->
    <div
      :key="contentKey"
      style="margin: 10px 5px;border-radius: 5px;position:fixed;top:116px;bottom:0;left:0;right:0;overflow-y:scroll;"
    >
      <div
        v-for="(item,i) in listArr"
        :key="i"
      >
        <div
          class="all"
          @click="toggle(item)"
        >
          <span
            style="margin-left:20px;margin-left:20px;display: flex;align-items: center;"
            @click.stop="goNext(item)"
          >{{ item.y }}年{{ item.m }}月
            <van-icon
              v-if="num == 1"
              name="arrow"
            />
          </span>
          <span v-show="item.statu==false">
            <img
              style="width:20px;height:20px;vertical-align:middle;"
              src="@/assets/right.png"
              alt
            />
          </span>
          <span v-show="item.statu==true">
            <img
              style="width:20px;height:20px;vertical-align:middle;"
              src="@/assets/bottom.png"
              alt
            />
          </span>
          <span
            class="money"
            v-if="priceAll[item.y+'-'+item.m]"
            @click.stop="goNext(item)"
          >
            <span style="display:inline-block;-width:122px;text-align:left;">
              <span style="color:#888;">总提成&ensp;</span>
              <span>&yen;{{ priceAll[item.y+'-'+item.m] }}</span>
            </span>&emsp;
          </span>
        </div>
        <div>
          <ul
            v-show="item.statu"
            :key="key"
            style="margin-bottom:10px;"
          >
            <li
              v-for="(item_o,index) in options[item.y+'-'+item.m]"
              :key="index"
              class="other_box"
              @click="goNext(item_o)"
            >
              <span class="li-box">
                <span style="display:inline-block;width:30%;margin-left: 1rem;-border:1px solid red;">{{ item_o.billTime }}</span>
                <span style="display:inline-block;width:32%;color:#000;-border:1px solid red;">{{ item_o.status_name }}</span>
                <span style="display:inline-block;width:20%;color:#2E7BF7;-text-align:left;-border:1px solid red;">&yen;{{ item_o.commission }}</span>
                <img
                  style="width:20px;height:20px;padding:8px 7px 0px 0px;color:#000;"
                  src="@/assets/right.png"
                  alt
                />&nbsp;
              </span>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import cookie from '@/common/cookie.js';
import dayjs from 'dayjs';
import { GetBusinessBsmBillRecordList } from '@/api/api_business_bsm';
export default {
    components: {},
    data() {
        return {
            key: 0,
            listArr: [],
            item: '',
            statu: false,
            options: {},
            s: '',
            priceAll: {},
            refuseAll: {},
            selected: '1',
            contentKey: 0,
            timeaaa: null,
            currentMounth: null,
            outBill: null,
            num: '1'
        };
    },
    created() {
        this.$store.commit('setHeader', '业务提成');
        let day = dayjs().diff(dayjs('2021-06-01'), 'month');
        let current_time = new Date();
        let year = current_time.getFullYear();
        let mouth = current_time.getMonth() + 1 < 10 ? '0' + (current_time.getMonth() + 1) : current_time.getMonth() + 1;
        this.currentMounth = year + '-' + mouth;
        for (let i = 0; i < day+1; i++) {
            let tempMouth = mouth - i;
            let tempYear = year;
            if (tempMouth <= 0) {
                let abstempMouth = Math.abs(tempMouth);
                tempMouth = 12 - (abstempMouth % 12);
                let tempYJ = parseInt(abstempMouth / 12) + 1;
                tempYear = tempYear - tempYJ;
            }
            this.listArr.push({
                y: tempYear,
                m: tempMouth < 10 ? '0' + tempMouth : tempMouth,
                statu: i == 0 ? true : false
            });
        }
        this.toggle(this.listArr[0], 1);
    },
    methods: {
        toggle(item, first) {
            this.item = item;
            if (first == 1) {
                item.statu = true;
            } else {
                item.statu = !item.statu;
            }
            if (item.statu == true) {
                this.getprice(item.y, item.m);
            }
        },
        getprice(y, m) {
            this.hotelId = cookie.getCookie('currentID');
            let data = {
                billTime: y + '-' + m,
                userId: this.$route.query.userId ? this.$route.query.userId : ''
            };
            this.timeaaa = data.billTime;
            GetBusinessBsmBillRecordList(data).done((res) => {
                this.outBill = res.order;
                this.options[y + '-' + m] = [];
                this.options[y + '-' + m] = res.data;
                this.func(this.options[y + '-' + m], y + '-' + m);
                ++this.contentKey;
            });
        },
        func(options, ym) {
            let priceAll = 0;
            for (let i = 0; i < options.length; i++) {
                priceAll += options[i].commission;
                this.priceAll[ym] = priceAll.toFixed(2);
                ++this.key;
                if (options[i].status == 0) {
                    options[i].status_name = '已出账';
                } else if (options[i].status == 1) {
                    options[i].status_name = '已结算';
                } else if (options[i].status == 2) {
                    options[i].status_name = '已转账';
                } else if (options[i].status == 3) {
                    options[i].status_name = '转账失败';
                }
            }
        },
        goNext(item) {
            this.$router.push({
                path: '/commissionDetail',
                query: {
                    hotelId: this.hotelId,
                    billTime: item.id ? item.billTime : item.y + '-' + item.m,
                    userId: this.$route.query.userId ? this.$route.query.userId : '',
                    id: item.id
                }
            });
        }
    }
};
</script>

<style scoped>
ul {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
#status_box {
    margin: 10px 5px;
    line-height: 50px;
    background-color: #fff;
    border-radius: 5px;
}
.all {
    display: flex;
    justify-content: space-between;
    padding: 0px 10px 10px 10px;
    background-color: #fff;
    line-height: 40px;
    position: relative;
}
.money {
    font-size: 14px;
    position: absolute;
    top: 18px;
    left: 28px;
    display: flex;
}
.li-box {
    width: 100%;
    font-size: 0.8rem;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
}
.other_box {
    color: #888888;
    line-height: 2rem;
    border-bottom: 1px solid #f6f6f6;
}
select {
    border: solid 1px #000;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    outline: none;
}
select::-ms-expand {
    display: none;
}
.mint-navbar .mint-tab-item.is-selected {
    color: #ff7d41;
    margin-bottom: 0;
    border-bottom: 3px solid #ff7d41;
}
</style>



